'use client';

import { useState } from 'react';
import { FiEdit2, FiCheck, FiX, FiPlus, FiTrash2 } from 'react-icons/fi';

interface EditableRowProps {
  label: string;
  value: string;
  isEditing: boolean;
  onEdit: (value: string) => void;
  onCancel?: () => void;
  onSave?: () => void;
  onDelete?: () => void;
}

function EditableRow({
  label,
  value,
  isEditing,
  onEdit,
  onCancel,
  onSave,
  onDelete
}: EditableRowProps) {
  return (
    <div className="flex items-center py-2 border-b last:border-b-0 group">
      <div className="w-1/3 text-sm text-gray-600">{label}</div>
      <div className="w-2/3 flex items-center gap-2">
        {isEditing ? (
          <>
            <input
              type="text"
              value={value}
              onChange={(e) => onEdit(e.target.value)}
              className="flex-1 px-2 py-1 border rounded"
            />
            <button
              onClick={onSave}
              className="p-1 text-green-600 hover:text-green-700"
            >
              <FiCheck size={14} />
            </button>
            <button
              onClick={onCancel}
              className="p-1 text-red-600 hover:text-red-700"
            >
              <FiX size={14} />
            </button>
          </>
        ) : (
          <>
            <span className="flex-1">{value}</span>
            <button
              onClick={onSave}
              className="p-1 text-gray-400 hover:text-gray-600 opacity-0 group-hover:opacity-100"
            >
              <FiEdit2 size={14} />
            </button>
            {onDelete && (
              <button
                onClick={onDelete}
                className="p-1 text-red-400 hover:text-red-600 opacity-0 group-hover:opacity-100"
              >
                <FiTrash2 size={14} />
              </button>
            )}
          </>
        )}
      </div>
    </div>
  );
}

interface EditableTableProps {
  data: Record<string, any>;
  onUpdate: (key: string, value: string) => Promise<void>;
  onDelete?: (key: string) => Promise<void>;
  excludeKeys?: string[];
  allowAdd?: boolean;
  onAdd?: (key: string, value: string) => Promise<void>;
}

export default function EditableTable({
  data,
  onUpdate,
  onDelete,
  excludeKeys = [],
  allowAdd = false,
  onAdd
}: EditableTableProps) {
  const [editingKey, setEditingKey] = useState<string | null>(null);
  const [editValue, setEditValue] = useState('');
  const [isAdding, setIsAdding] = useState(false);
  const [newKey, setNewKey] = useState('');
  const [newValue, setNewValue] = useState('');

  const handleSave = async (key: string) => {
    try {
      await onUpdate(key, editValue);
      setEditingKey(null);
    } catch (error) {
      console.error('Error saving:', error);
    }
  };

  const handleAdd = async () => {
    try {
      if (onAdd) {
        await onAdd(newKey, newValue);
        setIsAdding(false);
        setNewKey('');
        setNewValue('');
      }
    } catch (error) {
      console.error('Error adding:', error);
    }
  };

  return (
    <div className="bg-white rounded-lg p-4">
      {Object.entries(data)
        .filter(([key]) => !excludeKeys.includes(key) && key !== 'id')
        .map(([key, value]) => (
          <EditableRow
            key={key}
            label={key.split('-').map(word => 
              word.charAt(0).toUpperCase() + word.slice(1)
            ).join(' ')}
            value={editingKey === key ? editValue : String(value)}
            isEditing={editingKey === key}
            onEdit={setEditValue}
            onCancel={() => setEditingKey(null)}
            onSave={() => handleSave(key)}
            onDelete={onDelete ? () => onDelete(key) : undefined}
          />
        ))}
      
      {allowAdd && (
        <div className="mt-4">
          {isAdding ? (
            <div className="flex items-center gap-2">
              <input
                type="text"
                placeholder="Key"
                value={newKey}
                onChange={(e) => setNewKey(e.target.value)}
                className="flex-1 px-2 py-1 border rounded"
              />
              <input
                type="text"
                placeholder="Value"
                value={newValue}
                onChange={(e) => setNewValue(e.target.value)}
                className="flex-1 px-2 py-1 border rounded"
              />
              <button
                onClick={handleAdd}
                className="p-1 text-green-600 hover:text-green-700"
              >
                <FiCheck size={14} />
              </button>
              <button
                onClick={() => setIsAdding(false)}
                className="p-1 text-red-600 hover:text-red-700"
              >
                <FiX size={14} />
              </button>
            </div>
          ) : (
            <button
              onClick={() => setIsAdding(true)}
              className="text-sm text-[#ff6b4a] hover:text-[#ff5436] flex items-center gap-1"
            >
              <FiPlus /> Add Row
            </button>
          )}
        </div>
      )}
    </div>
  );
} 