export default function NamespaceList() {
  const [namespaces, setNamespaces] = useState<Namespace[]>([]);
  const [expandedId, setExpandedId] = useState<string | null>(null);
  const [accounts, setAccounts] = useState<{ [key: string]: NamespaceAccount[] }>({});
  const [methods, setMethods] = useState<{ [key: string]: NamespaceMethod[] }>({});
  const [loading, setLoading] = useState(true);
  const [expandedAccountId, setExpandedAccountId] = useState<string | null>(null);
  const [expandedMethodId, setExpandedMethodId] = useState<string | null>(null);

  useEffect(() => {
    loadNamespaces();
  }, []);

  useEffect(() => {
    if (expandedId) {
      loadAccountsAndMethods(expandedId);
    }
  }, [expandedId]);

  const loadNamespaces = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'namespace'));
      const data = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as Namespace[];
      setNamespaces(data);
    } catch (error) {
      console.error('Error loading namespaces:', error);
    } finally {
      setLoading(false);
    }
  };

  const loadAccountsAndMethods = async (namespaceId: string) => {
    try {
      // Load accounts
      const accountsQuery = query(
        collection(db, 'namespace-account'),
        where('namespace-id', '==', namespaceId)
      );
      const accountsSnapshot = await getDocs(accountsQuery);
      const accountsData = accountsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as NamespaceAccount[];
      setAccounts(prev => ({ ...prev, [namespaceId]: accountsData }));

      // Load methods
      const methodsQuery = query(
        collection(db, 'namespace-account-method'),
        where('namespace-id', '==', namespaceId)
      );
      const methodsSnapshot = await getDocs(methodsQuery);
      const methodsData = methodsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as NamespaceMethod[];
      setMethods(prev => ({ ...prev, [namespaceId]: methodsData }));
    } catch (error) {
      console.error('Error loading accounts and methods:', error);
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteDoc(doc(db, 'namespace', id));
      setNamespaces(namespaces.filter(n => n.id !== id));
    } catch (error) {
      console.error('Error deleting namespace:', error);
    }
  };

  const handleDeleteAccount = async (id: string, namespaceId: string) => {
    try {
      await deleteDoc(doc(db, 'namespace-account', id));
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error deleting account:', error);
    }
  };

  const handleDeleteMethod = async (id: string, namespaceId: string) => {
    try {
      await deleteDoc(doc(db, 'namespace-account-method', id));
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error deleting method:', error);
    }
  };

  const handleUpdateNamespace = async (key: string, value: string, namespaceId: string) => {
    try {
      await updateDoc(doc(db, 'namespace', namespaceId), {
        [key]: value
      });
      loadNamespaces();
    } catch (error) {
      console.error('Error updating namespace:', error);
    }
  };

  const handleUpdateAccount = async (key: string, value: string, accountId: string, namespaceId: string) => {
    try {
      await updateDoc(doc(db, 'namespace-account', accountId), {
        [key]: value
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating account:', error);
    }
  };

  const handleUpdateMethod = async (key: string, value: string, methodId: string, namespaceId: string) => {
    try {
      await updateDoc(doc(db, 'namespace-account-method', methodId), {
        [key]: value
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating method:', error);
    }
  };

  const handleAddAccount = async (namespaceId: string) => {
    try {
      await addDoc(collection(db, 'namespace-account'), {
        'namespace-id': namespaceId,
        'namespace-account-id': uuidv4(),
        'namespace-account-name': '',
        'namespace-account-url-override': '',
        'namespace-account-header': []
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error adding account:', error);
    }
  };

  const handleAddMethod = async (namespaceId: string) => {
    try {
      await addDoc(collection(db, 'namespace-account-method'), {
        'namespace-id': namespaceId,
        'namespace-account-method-id': uuidv4(),
        'namespace-account-method-name': '',
        'namespace-account-method-url-override': '',
        'namespace-account-method-queryParams': []
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error adding method:', error);
    }
  };

  if (loading) return <div>Loading...</div>;

  return (
    <div className="space-y-4">
      {namespaces.map((namespace) => (
        <div key={namespace.id} className="border rounded-lg bg-white">
          <ExpandableCard
            title={namespace['namespace-name']}
            subtitle={namespace['namespace-url']}
            isExpanded={expandedId === namespace.id}
            onToggle={() => setExpandedId(expandedId === namespace.id ? null : namespace.id)}
          >
            <div className="space-y-6">
              <div>
                <h4 className="font-medium mb-2">Namespace Details</h4>
                <EditableTable
                  data={namespace}
                  onUpdate={(key, value) => handleUpdateNamespace(key, value, namespace.id)}
                  excludeKeys={['id']}
                />
              </div>

              <div>
                <div className="flex justify-between items-center mb-2">
                  <h4 className="font-medium">Accounts</h4>
                  <button
                    onClick={() => handleAddAccount(namespace.id)}
                    className="text-sm text-[#ff6b4a] hover:text-[#ff5436] flex items-center gap-1"
                  >
                    <FiPlus /> Add Account
                  </button>
                </div>
                <div className="space-y-2">
                  {accounts[namespace.id]?.map(account => (
                    <ExpandableCard
                      key={account.id}
                      title={account['namespace-account-name']}
                      subtitle={account['namespace-account-url-override']}
                      isExpanded={expandedAccountId === account.id}
                      onToggle={() => setExpandedAccountId(
                        expandedAccountId === account.id ? null : account.id
                      )}
                    >
                      <EditableTable
                        data={account}
                        onUpdate={(key, value) => handleUpdateAccount(key, value, account.id, namespace.id)}
                        excludeKeys={['id', 'namespace-id']}
                      />
                    </ExpandableCard>
                  ))}
                </div>
              </div>

              <div>
                <div className="flex justify-between items-center mb-2">
                  <h4 className="font-medium">Methods</h4>
                  <button
                    onClick={() => handleAddMethod(namespace.id)}
                    className="text-sm text-[#ff6b4a] hover:text-[#ff5436] flex items-center gap-1"
                  >
                    <FiPlus /> Add Method
                  </button>
                </div>
                <div className="space-y-2">
                  {methods[namespace.id]?.map(method => (
                    <ExpandableCard
                      key={method.id}
                      title={method['namespace-account-method-name']}
                      subtitle={method['namespace-account-method-url-override']}
                      isExpanded={expandedMethodId === method.id}
                      onToggle={() => setExpandedMethodId(
                        expandedMethodId === method.id ? null : method.id
                      )}
                    >
                      <EditableTable
                        data={method}
                        onUpdate={(key, value) => handleUpdateMethod(key, value, method.id, namespace.id)}
                        excludeKeys={['id', 'namespace-id']}
                      />
                    </ExpandableCard>
                  ))}
                </div>
              </div>
            </div>
          </ExpandableCard>
        </div>
      ))}
    </div>
  );
} 